  methods: {
    slides(){
      return DataTruyen.getTruyenTrangChu();
    },
    getlink(){
      return "https://www.youtube.com/embed/" +this.youtube_parser(DataVideo.getlistvideo());
    },
    youtube_parser(url) {
      var regExp = /^.*((youtu.be\/)|(v\/)|(\/u\/\w\/)|(embed\/)|(watch\?))\??v?=?([^#&?]*).*/;
      var match = url.match(regExp);
      return (match && match[7].length == 11) ? match[7] : "";

    },
  }
}
